@font-face {
  font-family: "DM Sans";
  src: local("DMSans-VariableFont_opsz,wght"), url(DMSans-VariableFont_opsz,wght.ttf), format("ttf");
}
@font-face {
  font-family: "DM Sans-Regular";
  src: local("DMSans-Regular"), url(../fonts/static/DMSans-Regular.ttf), format("ttf");
}
@font-face {
  font-family: "DM Sans-MediumItalic";
  src: local("DMSans-MediumItalic"), url(../fonts/static/DMSans-MediumItalic.ttf), format("ttf");
}
@font-face {
  font-family: "DM Sans-Medium";
  src: local("DMSans-Medium"), url(../fonts/static/DMSans-Medium.ttf), format("ttf");
}
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: rgb(247.35, 247.35, 247.35);
  font-family: "DM Sans", sans-serif;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2 {
  margin: 0;
  line-height: 0.8;
  font-weight: 500;
  font-family: "DM Sans-Medium", sans-serif;
}

h1 {
  font-size: 4.5rem;
}

h2 {
  font-size: 3rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.3rem;
  }
}

[class$=heading] {
  margin: 0 0 2rem 0;
}

p {
  line-height: 1;
  font-family: "DM Sans-Regular", sans-serif;
}

.cl-negro {
  color: #000;
}

.cl-blanco {
  color: #FFFFFF;
}

.recuadros {
  margin: 2rem auto;
}
@media (min-width: 768px) {
  .recuadros {
    margin: 0;
    display: flex;
    justify-content: center;
  }
}
@media (min-width: 768px) {
  .recuadros__grid {
    display: grid;
    grid-template-rows: 12rem 12rem 11rem 11rem 11rem 11rem;
    grid-template-columns: repeat(4, 24rem);
    gap: 2rem;
  }
}

.recuadro {
  border-radius: 1rem;
  margin-bottom: 3rem;
}
.recuadro:last-of-type {
  margin: 0;
}
@media (min-width: 768px) {
  .recuadro {
    margin: 0;
  }
}
.recuadro-1 {
  background-color: hsl(256, 67%, 59%);
  text-align: center;
  padding: 2.5rem;
}
.recuadro-1__cadena {
  margin: 0;
  display: inline-block;
  font-family: "DM Sans-MediumItalic", sans-serif;
}
.recuadro-1__heading {
  font-size: 5rem;
}
.recuadro-1__heading span {
  color: hsl(39, 100%, 71%);
}
.recuadro-1__imagen {
  margin: 2rem auto 1rem auto;
  width: 70%;
}
@media (min-width: 768px) {
  .recuadro-1__imagen {
    width: 35%;
  }
}
.recuadro-1__texto {
  margin: 0;
}
@media (min-width: 768px) {
  .recuadro-1 {
    grid-column: 2/4;
    grid-row: 1/3;
    padding: 5rem 4rem;
  }
}
.recuadro-2 {
  background-color: #FFFFFF;
  padding: 2rem;
}
@media (min-width: 768px) {
  .recuadro-2 {
    grid-column: 2/3;
    grid-row: 3/5;
  }
}
@media (min-width: 768px) {
  .recuadro-2__heading {
    margin-top: 2rem;
    width: 90%;
  }
}
.recuadro-3 {
  background-color: hsl(39, 100%, 71%);
  padding: 2rem 2rem 0 2rem;
  overflow: hidden;
}
.recuadro-3__heading {
  width: 99%;
}
.recuadro-3__imagen {
  width: 70%;
}
@media (min-width: 768px) {
  .recuadro-3__imagen {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .recuadro-3 {
    grid-column: 3/4;
    grid-row: 3/5;
  }
}
.recuadro-4 {
  background-color: hsl(254, 88%, 90%);
  padding: 3rem;
  overflow: hidden;
}
@media (min-width: 768px) {
  .recuadro-4__imagen {
    width: 155%;
  }
}
.recuadro-4__texto {
  text-align: center;
}
@media (min-width: 768px) {
  .recuadro-4__texto {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .recuadro-4 {
    grid-column: 4/5;
    grid-row: 1/5;
    padding: 3rem 3rem;
  }
}
.recuadro-5 {
  background-color: hsl(256, 67%, 59%);
  padding: 2rem;
}
.recuadro-5__heading {
  margin: 3rem 0 0 0;
  text-align: center;
}
@media (min-width: 768px) {
  .recuadro-5__heading {
    text-align: left;
    margin: 0 0 0 1rem;
  }
}
@media (min-width: 768px) {
  .recuadro-5 {
    grid-column: 3/5;
    grid-row: 5/7;
    display: flex;
    align-items: center;
  }
}
.recuadro-6 {
  background-color: #FFFFFF;
  padding: 3rem 2rem;
}
.recuadro-6__heading {
  font-size: 5rem;
  margin: 0;
}
@media (min-width: 768px) {
  .recuadro-6__texto {
    font-size: 1.6rem;
  }
}
.recuadro-6__imagen {
  width: 60%;
}
@media (min-width: 768px) {
  .recuadro-6__imagen {
    width: 90%;
  }
}
@media (min-width: 768px) {
  .recuadro-6 {
    grid-column: 2/3;
    grid-row: 5/7;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
.recuadro-7 {
  background-color: hsl(39, 100%, 91%);
  padding: 2rem;
}
.recuadro-7__heading span {
  color: hsl(256, 67%, 59%);
  font-family: "DM Sans-MediumItalic", sans-serif;
}
.recuadro-7__imagen {
  width: 70%;
}
@media (min-width: 768px) {
  .recuadro-7 {
    grid-column: 1/2;
    grid-row: 1/4;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5rem 2rem;
  }
}
.recuadro-8 {
  background-color: hsl(39, 100%, 71%);
  padding: 2rem;
}
.recuadro-8__imagen {
  width: 80%;
}
@media (min-width: 768px) {
  .recuadro-8__imagen {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .recuadro-8 {
    grid-row: 4/7;
  }
}

.attribution {
  padding: 0 2rem;
  margin-top: 2rem;
  text-align: center;
}
.attribution a {
  text-decoration: none;
  color: hsl(256, 67%, 59%);
  font-weight: 700;
}